.aboutGarden {
  width: 100%; /* Ensure the div has a width */
  height: 100%; /* Set a height for the div */
  background-size: contain; /* Cover the entire area */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Prevent repeating */
  transform: rotate(-2deg); 
}

.aboutVancouver {
  width: 100%; /* Ensure the div has a width */
  height: 100%; /* Set a height for the div */
  background-size: contain; /* Cover the entire area */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Prevent repeating */
  transform: rotate(0deg); 
}


.fadeAboutAnimation{
  animation-name: fadeAbout;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

/* The animation code */
@keyframes fadeAbout {
  from {
      opacity: 0%;
  }
  25% {
      opacity: 0%;
  }
  to {
      opacity: 100%;
  }
}



.aboutScrollableSec {
  height:100%;
  overflow-y: auto; /* allows vertical scrolling */
  overflow-x: hidden; /* hides horizontal scrolling */
  /* Alternatively, use overflow: auto; to enable both vertical and horizontal scrolling if needed */
}