

.newsPage{
  width: 90%;
}

.newsHeader{
  height: 5%;
  width: 100%;
}



.fadeNewsAnimation{
  animation-name: fadeNews;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

/* The animation code */
@keyframes fadeNews {
  from {
      opacity: 0%;
  }
  25% {
      opacity: 0%;
  }
  to {
      opacity: 100%;
  }
}

.newsContent1 {
  height: 35%;
  width: 100%;
}

.newsPage p{
  text-align: left;
  margin-left: 3%;
  color: black;
}


#newsDateBlock{
  background-color: white;
  float:left;
  font-family: 'Urbanist';
  padding: 2px;
  font-size: 25px;
  border-radius: 10px;
  position:relative;
  margin-left: 3%;
  height: 35px;
  width: 10%;
  text-align: center;
  margin-top: 5px;
}

#newsNewsBlock{
  float:left;
  font-family: 'Urbanist';
  padding: 10px;
  font-size: 25px;
  border-radius: 10px;
  position:relative;
  margin-left: 20px;
  /* white-space: nowrap; */
  overflow: hidden;
  width: 80%;
  text-align: left;
}



.newsScrollableSec {
  height:100%;
  overflow-y: auto; /* allows vertical scrolling */
  overflow-x: hidden; /* hides horizontal scrolling */
  /* Alternatively, use overflow: auto; to enable both vertical and horizontal scrolling if needed */
}