.researchPage{
  width: 90%;
}



.researchPic {
  background-size: cover; /* Ensure the image covers the entire div */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Prevent repeating of the image */
  width: 100%;
  height: 100%;
  margin-right: 0%;
  display: flex;
  flex-direction: column; /* Stack text vertically */
  align-items: center; /* Center horizontally within this container */
  justify-content: center; /* Center vertically within this container */
}


.researchBio {
  width: 90%;
  text-align: left !important;
  margin: 5%;
}

.researchSection p {
  margin: 5px 0; /* Remove default margins */
}




.researchName {
  font-weight: bold;
  font-family: 'Roboto', sans-serif; /* Change 'Arial' to the desired font */
  font-size: 1.2em; /* Adjust size as needed */

  text-align: left !important;
}

.researchTitle {
  font-weight: bold;
  font-family: 'Urbanist', sans-serif; /* Change 'Arial' to the desired font */
  text-align: left !important;
}

.links a {
  text-decoration: none;
  color: #123456; /* Link color */
  font-family: 'Urbanist', sans-serif; /* Change 'Arial' to the desired font */
}

.links a:hover {
  text-decoration: underline; /* Underline on hover */
}

.fadeResearchAnimation{
  animation-name: fadeResearch;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

/* The animation code */
@keyframes fadeResearch {
  from {
      opacity: 0%;
  }
  25% {
      opacity: 0%;
  }
  to {
      opacity: 100%;
  }
}

h3 {
  text-align: left;
  margin-left: 3%;
}

.researchIcon{
  float: left;
  width: 29%;
  background-color: rgba(127, 193, 255, 0.792);
  height: 50%;
  margin-bottom: 3%;
  margin-left: 3%;
  border-radius: 10px;
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center; /* Center horizontally */
}

.researchIcon2{
  float: left;
  width: 29%;
  background-color: rgba(70, 196, 171, 0.792);
  aspect-ratio: 2.5;
  margin-bottom: 3%;
  margin-left: 3%;
  border-radius: 10px;
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center; /* Center horizontally */
}

.researchIcon3{
  float: left;
  width: 44%;
  background-color: rgba(229, 225, 235, 0.792);
  height: 250px;
  margin-bottom: 3%;
  margin-left: 3%;
  border-radius: 10px;
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center; /* Center horizontally */
}

.researchLabel{
  margin-top: 1%;
  width:100%;
  clear:both;
  background-color: white;
}


.researchscrollableSec {
  height:100%;
  overflow-y: auto; /* allows vertical scrolling */
  overflow-x: hidden; /* hides horizontal scrolling */
  /* Alternatively, use overflow: auto; to enable both vertical and horizontal scrolling if needed */
}

.researchFooter{
  height: 20px;
  clear:both;
}

.researchHeader{
  height: 5%;
  width: 100%;
}

.researchPage p{
  text-align: left;
  margin-left: 3%;
  margin-bottom: 3%;
}

