.peoplePage{
  width: 90%;
}



.peoplePic {
  /* width: 150px; /* Set the width of the circle */
  /* height: 150px; Set the height of the circle */ 
  background-size: cover; /* Ensure the image covers the entire div */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Prevent repeating of the image */
  border-radius: 50%; /* Make the div circular */
  height: 90%;
  width: 90%;
  margin: 5%;
  margin-right: 0%;
  width:40%;
  float:left;
  display: flex;
  flex-direction: column; /* Stack text vertically */
  align-items: center; /* Center horizontally within this container */
  justify-content: center; /* Center vertically within this container */
}


.peopleBio {
  display: flex;
  flex-direction: column; /* Stack text vertically */
  align-items: center; /* Center horizontally within this container */
  justify-content: center; /* Center vertically within this container */
  width: 55%;
}

.peopleBio p {
  margin: 5px 0; /* Remove default margins */
}




.name {
  font-weight: bold;
  font-family: 'Roboto', sans-serif; /* Change 'Arial' to the desired font */
  font-size: 1.2em; /* Adjust size as needed */
}

.title {
  font-weight: bold;
  font-family: 'Urbanist', sans-serif; /* Change 'Arial' to the desired font */
}

.links a {
  text-decoration: none;
  color: #123456; /* Link color */
  font-family: 'Urbanist', sans-serif; /* Change 'Arial' to the desired font */
}

.links a:hover {
  text-decoration: underline; /* Underline on hover */
}

.fadePeopleAnimation{
  animation-name: fadePeople;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

/* The animation code */
@keyframes fadePeople {
  from {
      opacity: 0%;
  }
  25% {
      opacity: 0%;
  }
  to {
      opacity: 100%;
  }
}

h3 {
  text-align: left;
  margin-left: 3%;
}

