

.joinPage{
  width: 80%;
}

.joinHeader{
  height: 10%;
  width: 100%;
}



.fadeJoinAnimation{
  animation-name: fadeJoin;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

/* The animation code */
@keyframes fadeJoin {
  from {
      opacity: 0%;
  }
  25% {
      opacity: 0%;
  }
  to {
      opacity: 100%;
  }
}

.joinContent1 {
  height: 35%;
  width: 100%;
}

.joinPage p{
  text-align: left;
  margin-left: 3%;
  color: black;
}




.joinScrollableSec {
  height:100%;
  overflow-y: auto; /* allows vertical scrolling */
  overflow-x: hidden; /* hides horizontal scrolling */
  /* Alternatively, use overflow: auto; to enable both vertical and horizontal scrolling if needed */
}